import React, { ChangeEvent, useState } from 'react'
import styled from 'styled-components'
import { sc } from 'src/styles'
import { TextInput } from 'src/components'
import { DataItem } from 'src/types'
import items from 'src/data.json'

import Collected from './Collected'
import ResultItem from './ResultItem'

const App = () => {
  const [search, setSearch] = useState('')
  const [results, setResults] = useState<DataItem[]>([])

  const onSearch = (ev: ChangeEvent<HTMLInputElement>) => {
    const val = ev.target.value
    setSearch(val)

    if (!val) {
      setResults([])
    } else {
      const filtered: DataItem[] = (items as DataItem[]).filter(
        item => compare(item.shortName, val) || compare(item.name, val)
      )
      setResults(filtered)
    }
  }

  return (
    <Container>
      <Collected />

      <Search>
        <TextInput
          value={search}
          onChange={onSearch}
        />

        { results.length ? (
          <Results>
            { results.map((item: DataItem) => (
              <li key={item.id}>
                <ResultItem item={item} />
              </li>
            )) }
          </Results>
        ) : null }
      </Search>
    </Container>
  )
}

export default App

const compare = (str1: string, str2: string): boolean => {
  return str1.toLowerCase().includes(str2.toLowerCase())
}

const Container = styled.div`
  display: flex;
  gap: ${sc.spacing(2)};
  padding: ${sc.spacing(3)};
`

const Search = styled.div`
  flex: 6;
`

const Results = styled.ul`
  margin-top: ${sc.spacing(2)};
  border: 1px solid ${sc.secondary};

  & > li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: ${sc.spacing(1)};
    padding-left: ${sc.spacing(2)};
  }
`
