import React, { useState } from 'react'
import { DataProvider } from './context'
import { StorageData } from './types'
import App from './views/App'

// TODO: constants
const storage: StorageData = JSON.parse(localStorage.getItem('tarkov-tracker-data') || '{}')

export default () => {
  const [data, setData] = useState(storage)

  return (
    <DataProvider value={{ data, setData }}>
      <App />
    </DataProvider>
  )
}
