import React, { ChangeEvent } from 'react'
import styled from 'styled-components'
import { TextInput } from 'src/components'
import { useData } from 'src/context'
import { sc } from 'src/styles'
import { DataItem } from 'src/types'

import { getQuestQuant, getHideoutQuant } from '../helpers'

type Props = {
  item: DataItem
}

export default ({ item }: Props) => {
  const { data, add, update } = useData()

  const onAdd = () => add(item.id)

  const onChange = (ev: ChangeEvent<HTMLInputElement>) => {
    const val = ev.target.value

    if (!val) {
      update(item.id, 0)
    } else {
      update(item.id, Number(val))
    }
  }

  return (
    <>
      <span>{item.name} ({item.shortName})</span>
      <Actions>
        <DoneInput
          value={String(data[item.id] || 0)}
          onChange={onChange}
        />

        <Total type="quest" onClick={onAdd}>
          {getQuestQuant(item)}
        </Total>

        <Total type="hideout" onClick={onAdd}>
          {getHideoutQuant(item)}
        </Total>
      </Actions>
    </>
  )
}

const Actions = styled.div`
  display: flex;
  gap: ${sc.spacing(1)};
`

const Total = styled.div<{
  type: 'quest' | 'hideout'
}>`
  width: ${sc.spacing(6)};
  height: ${sc.spacing(6)};
  line-height: ${sc.spacing(5.5)};
  color: ${sc.fontContrast};
  font-weight: 600;
  border-radius: 3px;
  background-color: ${props => props.type === 'quest' ? sc.quest : sc.hideout};
  text-align: center;
  user-select: none;
  cursor: pointer;
`

const DoneInput = styled(TextInput)`
  width: ${sc.spacing(6)};
  border-radius: 4px;
  text-align: center;
`
