import { useContext } from 'react'
import { context } from './context'

export default () => {
  const { data, setData } = useContext(context)

  return {
    data,
    add: (key: string) => {
      const updated = { ...data }

      if (!updated[key]) {
        updated[key] = 0
      }

      updated[key] += 1
      setData(updated)
      // TODO: constants
      localStorage.setItem('tarkov-tracker-data', JSON.stringify(updated))
    },
    update: (key: string, value: number) => {
      const updated = { ...data }

      if (!value) {
        delete updated[key]
      } else {
        updated[key] = value
      }

      setData(updated)
      // TODO: constants
      localStorage.setItem('tarkov-tracker-data', JSON.stringify(updated))
    }
  }
}
