import { DataItem } from 'src/types'

export const getQuestQuant = (item: DataItem): number => {
  if (!item.quests?.length) {
    return 0
  }

  return item.quests.reduce((acc, quest) => acc += quest.quantity, 0)
}

export const getHideoutQuant = (item: DataItem): number => {
  if (!item.hideout?.length) {
    return 0
  }

  return item.hideout.reduce((acc, hideout) => acc += hideout.quantity, 0)
}
